<template>
  <div>
    <el-row :gutter="20" class="mgb20">
      <el-col :span="6">
        <el-card shadow="hover" :body-style="{ padding: '0px' }">
          <div class="grid-content grid-con-1">
            <div class="grid-cont-right">
              <div class="grid-num">{{ userCount }}</div>
              <div>Total users</div>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card shadow="hover" :body-style="{ padding: '0px' }">
          <div class="grid-content grid-con-2">
            <div class="grid-cont-right">
              <div class="grid-num">{{ songCount }}</div>
              <div>Total songs</div>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card shadow="hover" :body-style="{ padding: '0px' }">
          <div class="grid-content grid-con-3">
            <div class="grid-cont-right">
              <div class="grid-num">{{ singerCount }}</div>
              <div>Total singers</div>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card shadow="hover" :body-style="{ padding: '0px' }">
          <div class="grid-content grid-con-4">
            <div class="grid-cont-right">
              <div class="grid-num">{{ songListCount }}</div>
              <div>Total song lists</div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <h3 style="margin-bottom: 20px">User Gender Ratio</h3>
        <div class="cav-info" style="background-color: white">
          <ve-pie :data="userSex" :theme="options"></ve-pie>
        </div>
      </el-col>
      <el-col :span="12">
        <h3 style="margin-bottom: 20px">Song Type Distribution</h3>
        <div class="cav-info" style="background-color: white">
          <ve-histogram :data="songStyle" :theme="options3"></ve-histogram>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <h3 style="margin: 20px 0">Singer Gender Ratio</h3>
        <div class="cav-info" style="background-color: white">
          <ve-pie :data="singerSex" :theme="options1"></ve-pie>
        </div>
      </el-col>
      <el-col :span="12">
        <h3 style="margin: 20px 0">Singer Nationality Distribution</h3>
        <div class="cav-info" style="background-color: white">
          <ve-histogram :data="country" :theme="options2"></ve-histogram>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mixin } from "../mixins";
import { HttpManager } from "../api/index";

export default {
  mixins: [mixin],
  data() {
    return {
      user: [],
      userSex: {
        columns: ["Gender", "total"],
        rows: [
          { Gender: "Male", total: 0 },
          { Gender: "Female", total: 0 }
        ]
      },
      singerSex: {
        columns: ["Gender", "total"],
        rows: [
          { Gender: "Male", total: 0 },
          { Gender: "Female", total: 0 }
        ]
      },
      country: {
        columns: ["Country", "total"],
        rows: [
          { Country: "China", total: 0 },
          { Country: "Korea", total: 0 },
          { Country: "Italy", total: 0 },
          { Country: "Singapore", total: 0 },
          { Country: "America", total: 0 },
          { Country: "Malaysia", total: 0 },
          { Country: "Spain", total: 0 },
          { Country: "Japan", total: 0 }
        ]
      },
      options: {
        color: ["#87CEFA", "#FFC0CB"]
      },
      options1: {
        color: ["#1E90FF", "#7B68EE"]
      },
      options2: {
        color: ["#FEED78"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        }
      },
      options3: {
        color: ["#FD8A61"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        }
      },
      songStyle: {
        columns: ["Style", "total"],
        rows: [
          { Style: "华语", total: 0 },
          { Style: "粤语", total: 0 },
          { Style: "欧美", total: 0 },
          { Style: "日韩", total: 0 },
          { Style: "BGM", total: 0 },
          { Style: "轻音乐", total: 0 },
          { Style: "乐器", total: 0 }
        ]
      },
      userCount: 0,
      songCount: 0,
      singerCount: 0,
      songListCount: 0
    };
  },
  mounted() {
    this.getUser();
    this.getSinger();
    this.getSong();
    this.getSongList();
  },
  methods: {
    getUser() {
      HttpManager.getAllUser().then(res => {
        this.userCount = res.length;
        this.userSex.rows[0]["total"] = this.setSex(1, res);
        this.userSex.rows[1]["total"] = this.setSex(0, res);
      });
    },
    setSex(sex, arr) {
      let count = 0;
      for (let item of arr) {
        if (sex === item.sex) {
          count++;
        }
      }
      return count;
    },
    getCountry(val) {
      for (let item of this.country.rows) {
        if (val.includes(item["Country"])) {
          item["total"]++;
          break;
        }
      }
    },
    getStyle(val) {
      for (let item of this.songStyle.rows) {
        if (val.includes(item["Style"])) {
          item["total"]++;
        }
      }
    },
    getSinger() {
      HttpManager.getAllSinger()
        .then(res => {
          this.singerCount = res.length;
          this.singerSex.rows[0]["total"] = this.setSex(1, res);
          this.singerSex.rows[1]["total"] = this.setSex(0, res);
          for (let item of res) {
            this.getCountry(item.location);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    getSong() {
      HttpManager.getAllSong()
        .then(res => {
          this.songCount = res.length;
        })
        .catch(err => {
          console.log(err);
        });
    },
    getSongList() {
      HttpManager.getSongList()
        .then(res => {
          this.songListCount = res.length;
          for (let item of res) {
            this.getStyle(item.style);
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style>
.grid-content {
  display: flex;
  align-items: center;
  height: 100px;
}

.grid-cont-right {
  flex: 1;
  text-align: center;
  font-size: 14px;
  color: #999;
}

.grid-num {
  font-size: 30px;
  font-weight: bold;
}

.cav-info {
  border-radius: 6px;
  overflow: hidden;
}
</style>



